// html hot action
'use strict';
let hotClient = require('webpack-hot-middleware/client?noInfo=true&reload=true');

// 订阅事件，当 event.action === 'reload' 时执行页面刷新
hotClient.subscribe(function(event) {
    if (event.action === 'reload') {
        window.location.reload();
    }
});



